<template>
    <div class="editor">
        <div class="input-group">
            <div class="input-group-btn">
                <button type="button" class="btn btn-success"></button>
            </div>
            <input
                type="text"
                v-model="messageTitle"
                class="form-control"
                placeholder="... ..."
            />
        </div>

        <div>
            <p></p>
        </div>

        <div ref="toolbar" class="toolbar"></div>

        <div ref="editor" class="text"></div>

        <div>
            <p></p>
        </div>

        <Button class="btn btn-success pull-right" @click="submit"></Button>
    </div>
</template>

<script>
import E from "wangeditor";
export default {
    name: "editoritem",
    data() {
        return {
            messageTitle: "",
            messageContent: "",
            // uploadPath,
            editor: null,
            info_: null
        };
    },
    model: {
        // prop: "value",
        event: "publishMessage"
    },
    props: {
        // value: {
        //     type: String,
        //     default: ""
        // },
        isClear: {
            type: Boolean,
            default: false
        }
    },
    watch: {
        isClear(val) {
            // 
            if (val) {
                this.editor.txt.clear();
                this.info_ = null;
            }
        }
        // value: function(value) {
        //     if (value !== this.editor.txt.html()) {
        //         this.editor.txt.html(this.value);
        //     }
        // }
        //valuevalue
    },
    mounted() {
        this.initEditor();
    },
    methods: {
        initEditor() {
            // http://192.168.2.125:8080/admin/storage/create
            this.editor = new E(this.$refs.toolbar, this.$refs.editor);
            this.editor.customConfig.uploadImgShowBase64 = false; // base 64 
            this.editor.customConfig.uploadImgServer =
            "http://127.0.0.1:5000/api/message.upload_img/";
            // "http://otp.cdinfotech.top/file/upload_images"; // 
            this.editor.customConfig.uploadImgHeaders = {}; //  header
            this.editor.customConfig.uploadFileName = "file"; // 
            this.editor.customConfig.uploadImgMaxSize = 2 * 1024 * 1024; //  2M
            this.editor.customConfig.uploadImgMaxLength = 6; //  3 
            this.editor.customConfig.uploadImgTimeout = 3 * 60 * 1000; // 

            // 
            this.editor.customConfig.menus = [
                "head", // 
                "bold", // 
                "fontSize", // 
                "fontName", // 
                "italic", // 
                "underline", // 
                "strikeThrough", // 
                "foreColor", // 
                "backColor", // 
                "link", // 
                "list", // 
                "justify", // 
                "quote", // 
                "emoticon", // 
                "image", // 
                "table", // 
                "video", // 
                "code", // 
                "undo", // 
                "redo", // 
                "fullscreen" // 
            ];

            this.editor.customConfig.uploadImgHooks = {
                fail: (xhr, editor, result) => {
                    // 
                    alert("");
                },
                success: (xhr, editor, result) => {
                    // 
                    alert("");
                },
                timeout: (xhr, editor) => {
                    // 
                    alert("");
                },
                error: (xhr, editor) => {
                    // 
                    alert("");
                },
                customInsert: (insertImg, result, editor) => {
                    // 
                    //resultdata[{url:""},...]
                    console.log(result);
                    //insertImg()
                    //
                    // for (let i = 0; i < 1; i++) {
                    // console.log(result)
                    // let url = "http://otp.cdinfotech.top" + result.url;
                    let url = "http://127.0.0.1:5000/" + result.url;
                    insertImg(url);
                    // }
                }
            };
            this.editor.customConfig.onchange = html => {
                this.messageContent = html; // 
            };
            // 
            this.editor.create();
        },
        submit() {
            this.$emit("publishMessage", {
                messageTitle: this.messageTitle,
                messageContent: this.messageContent
            });
        }
    }
};
</script>

<style lang="css">
.editor {
    width: 100%;
    margin: 0 auto;
    position: relative;
    z-index: 0;
}
.toolbar {
    border: 1px solid #ccc;
}
.text {
    border: 1px solid #ccc;
    min-height: 500px;
}
</style>